.welcome{
	position: relative;
	height: 885px;
	padding-top: 130px;
	background: url("../img/cover.jpg") no-repeat center;
	background-size: cover;
	@include mq($from: mobile, $until: tablet){
		padding-bottom: 15px;
		padding-top: 50px;
		height: 500px;
	}
	@include mq($from: tablet, $until: wide){
		padding-top: 80px;
		height: 700px;
	}
}

.eveint-info{
	display: flex;
	justify-content: space-around;
	margin-bottom: 210px;
	@include mq($from: mobile, $until: mobileHorizontal){
		flex-direction: column;
		margin-bottom: 40px;
	}
	@include mq($from: mobileHorizontal, $until: desktop){
		flex-direction: column;
		margin-bottom: 100px;
	}
	@include mq($from: desktop, $until: wide){
		margin-bottom: 90px;
	}
	&__item{
		@include mq($from: mobile, $until: tablet){
			margin-bottom: 25px;
			&:last-child{
				margin-bottom: 0;
			}
		}
		@include mq($from: tablet, $until: desktop){
			margin-bottom: 35px;
			&:last-child{
				margin-bottom: 0;
			}
		}
		h3{
			margin: 0 0 5px;
			color: #ffbd1f;
			font-size: 16px;
			line-height: 28px;
			text-align: center;
			font-weight: 500;
			letter-spacing: 3px;
			text-transform: uppercase;
			@include mq($from: mobile, $until: mobileHorizontal){
				font-size: 14px;
				line-height: 22px;
			}
		}
		div{
			color: #fff;
			font-size: 30px;
			line-height: 36px;
			font-weight: 300;
			text-align: center;
			@include mq($from: mobile, $until: mobileHorizontal){
				font-size: 24px;
				line-height: 30px;
			}
			span{
				display: block;
				font-size: 20px;
				color: #fff2d2;
				font-weight: 300;
				@include mq($from: mobile, $until: mobileHorizontal){
					font-size: 16px;
				}
			}
		}
	}
}

.organizer{
	color: #fff;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 20px;
	text-transform: uppercase;
	font-weight: normal;
	@include mq($from: mobile, $until: mobileVertical){
		font-size: 14px;
	}
	img{
		margin-left: 23px;
		@include mq($from: mobile, $until: tablet){
			margin-left: 0;
		}
	}
}


.skroll-arrow{
	position: absolute;
	bottom: -4px;
	left: 50%;
	transform: translateX(-50%);
	@include mq($from: mobile, $until: desktop){
		display: none;
	}
	&__ln{
		display: block;
	}
}