html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body{
	margin: 0;
	padding: 0;
	min-width: 320px;
	color: #4e4e4e;
	font-family: 'Roboto', sans-serif;
	font-size: 16px;
	line-height: 20px;
}


.container{
	max-width: 1170px;
	padding-left: 15px;
	padding-right: 15px;
	margin: 0 auto;
}

.logo-head{
	margin-bottom: 220px;
	@include mq($from: mobile, $until: desktop){
		margin-bottom: 50px;
	}
	&__wrap{
		margin: 0 auto;
		width: 395px;
		height: 45px;
		@include mq($from: mobile, $until: tablet){
			width: 250px;
			height: 40px;
		}
		img{
			display: block;
			@include mq($from: mobile, $until: tablet){
				width: 250px;
				height: 40px;
			}
		}
	} 
}


