.footer{
	padding: 20px 0;
	color: #878787;
	background: #1e1e1e;
	&-inner{
		display: flex;
		justify-content: space-between;
	}
	 
	.public-contract{
		@include mq($from: mobile, $until: desktop){
			margin-bottom: 25px;
		}
		a{
			display: block;
			text-align: center;
			color: #b4b4b4;
			font-size: 14px;
			text-decoration: none;
			cursor: pointer;
			transition: .2s ease-in-out;
			&:hover, &:focus{
				text-decoration: underline;
			}
		}
	}
	.contact{
		div{
			display: flex;
			align-items: center;
			margin-bottom: 15px;
			&:last-child{
				margin-bottom: 0;
			}
			i{
				font-size: 20px;
				color: #d1d1d1;
				margin: 0 10px 0 0;
			}
			a{
				display: block;
				text-decoration: none;
				font-size: 16px;
				color: #d1d1d1;
				font-weight: 400;
				transition: .2s ease-in-out;
				&:hover, &:focus{
					text-decoration: none;
					color: #ffbd1f;
				}
			}
		}
	}
}

.scroll-up {
    position: fixed;
    display: none;
    z-index: 999;
    bottom: 2%;
    right: 2%;
    .eee{
	    background-color: #3b3b3b;
	    display: block;
	    width: 40px;
	    height: 40px;
	    padding: 4px;
	    border-radius: 50%;
	    span{
	    	display: block;
			width: 33px;
			height: 33px;
	    	border: 1px solid #ffbd1f;
	    	text-align: center;
		    border-radius: 50%;
		    color: #fff;
		    font-size: 26px;
		    line-height: 30px;
		    transition: all 0.125s ease-in-out 0s;
		    cursor: pointer;
			&:hover{
			    background-color: #444343;
			    color: #ffbd1f;
			}
			&:active {
			    background-color: #444343;
			    color: #ffbd1f;
			}
	    }
	}
}