.events-program{
	padding-top: 80px;
	padding-bottom: 80px;  
	&__title{
		margin-bottom: 45px;
		h2{
			margin: 0 0 20px 0;
			font-size: 30px;
			line-height: 35px;
			color: #292929;
			text-align: center;
			letter-spacing: 1px;
			font-weight: bold;
		}
		span{
			display: block;
			margin: auto;
			width: 165px;
			height: 2px;
			background: #ffbd1f;
		}
	}
	&-start{
		color: #292929;
		text-align: center;
	    font-size: 17px;
	    font-weight: 500;
		text-transform: uppercase;
		time{
			display: block;
			margin-bottom: 18px;
			color: #ffbd1f;
			font-size: 34px;
			font-weight: bold;
			@include mq($from: mobile, $until: desktop){
				font-size: 22px;
				line-height: 26px;
				margin-bottom: 10px;
			}
		}
	}
	&__item{
		@include mq($from: tablet){
			padding-top: 75px;
			padding-bottom: 35px;
		}
		@include mq($from: mobile, $until: tablet){
			padding-top: 20px;
			padding-bottom: 20px;
		}
		&:first-child{
			padding-top: 75px;
		}
		&:nth-child(odd){
			.events-program__speaker{
				img{
					top: 20px;
					left: 20px;
					@include mq($from: tablet, $until: desktop){
						top: 10px;
						left: 10px;
					}
				}
				div{
					left: 280px;
					top: 75px;  
					@include mq($from: tablet, $until: desktop){
						left: 200px;
					}
				}
			}
		}
		&:nth-child(even){
			.events-program__speaker{
				order: 2;
				@include mq($from: mobile, $until: tablet){
					order: 1;
				}
				img{
					top: 20px;
					right: 20px;
					@include mq($from: tablet, $until: desktop){
						top: 10px;
						right: 10px;
					}
				}
				div{
					left: -130px;
					top: 75px; 
				}
			}
			.events-program__description{
				order: 1;
				@include mq($from: mobile, $until: tablet){
					order: 2;
				}
			}
		}
		&__inner{
			display: flex;
			max-width: 945px;
			margin: 0 auto;
			@include mq($from: tablet){
				align-items: center;
				justify-content: space-between;
			}
			@include mq($from: mobile, $until: tablet){
				flex-direction: column;
			}
		}
	}

	&__speaker{
		display: flex;
		width: 330px;
		height: 420px;
		border: 15px solid #ffbd1f;
		@include mq($from: mobile, $until: mobileVertical){
			margin: auto;
			width: 280px;
			height: 100%;
			flex-direction: column;
			border: none;
		}
		@include mq($from: mobileVertical, $until: tablet){
			width: 270px;
			height: 320px;
			border: none;
			margin-bottom: 10px;
		}
		@include mq($from: tablet, $until: desktop){
			width: 230px;
			height: 320px;
			border: 10px solid #ffbd1f;
		}
		@include mq($from: tablet){
			position: relative;
		}
		img{
			display: block;
			width: 324px;
			height: 418px;
			@include mq($from: mobile, $until: mobileVertical){
				border: 5px solid #ffbd1f;
				margin-bottom: 15px;
			}
			@include mq($from: mobileVertical, $until: tablet){
				width: 250px;
				height: 300px;
				border: 10px solid #ffbd1f;
			}
			@include mq($from: mobile, $until: tablet){
				width: 100%;
				height: 100%;
			}
			@include mq($from: tablet, $until: desktop){
				width: 224px;
				height: 310px;
			}
			@include mq($from: tablet){
				position: absolute;
			}
		}
		div{
			@include mq($from: mobile, $until: mobileVertical){
				margin-bottom: 20px;
			}
			@include mq($from: mobileVertical, $until: tablet){
				margin-left: 20px;
			}
			@include mq($from: tablet){
				position: absolute;
			}
			h2{
				margin: 0 0 5px 0;
				color: #292929;
				line-height: 29px;
				font-size: 30px;
				font-weight: bold;
				@include mq($from: mobile, $until: desktop){
					font-size: 26px;
					line-height: 28px;
				}
			}
			span{
				display: block;
				color: #292929;
				font-size: 16px;
			}
		}	
	}
	&__description{
		time{
			display: block;
			color: #ffbd1f;
			font-size: 30px;
			margin-bottom: 20px;
			font-weight: bold;
			@include mq($from: mobile, $until: desktop){
				font-size: 22px;
				line-height: 26px;
			}
		}
		h3{
			margin: 0 0 20px 0;
			color: #292929;
			font-size: 17px;
			line-height: 22px;
			text-transform: uppercase;
			@include mq($from: tablet, $until: desktop){
				font-size: 14px;
			}
		}
		ul{
			margin: 0;
			padding: 0 0 0 25px;
			list-style: none;
			li{
				margin-bottom: 19px;
				font-size: 16px;
				line-height: 18px;
				font-weight: 400;
				color: #646464;
				position: relative;
				@include mq($from: mobile, $until: desktop){
					font-size: 14px;
					line-height: 20px;			
				}
				&:before{
					content: "";
					position: absolute;
					top: 10px;
					left: -22px;
					width: 15px;
					height: 2px;
					background: #838383;
				}
			}
		}
		&_img{
		    display: flex;
		    align-items: center;
    		justify-content: center;
		}
	}
	&-finish{
		margin-top: 75px;
		color: #292929;
		text-align: center;
	    font-size: 17px;
	    font-weight: 500;
		text-transform: uppercase;
		@include mq($from: mobile, $until: desktop){
			margin-top: 35px;
		}
		time{
			display: block;
			margin-bottom: 18px;
			color: #ffbd1f;
			font-size: 34px;
			@include mq($from: mobile, $until: desktop){
				font-size: 22px;
				line-height: 26px;
				margin-bottom: 10px;
			}
		}
	}
}