.partners{
	padding-top: 72px;
	padding-bottom: 74px;
	background: #f1f1f1;
	@include mq($from: mobile, $until: tablet){
		padding-top: 20px;
		padding-bottom: 10px;
	}
	&__title{
		margin-bottom: 45px;
		h2{
			margin: 0 0 20px 0;
			font-size: 30px;
			line-height: 35px;
			color: #292929;
			text-align: center;
			letter-spacing: 1px;
			font-family: 'Roboto', sans-serif;
			font-weight: bold;
		}
		span{
			display: block;
			margin: auto;
			width: 165px;
			height: 2px;
			background: #ffbd1f;
		}
	}
}

.partners-list{
	display: flex;
	align-items: center;
	padding: 0;
	list-style: none;
	@include mq($from: desktop){
		align-items: center;
		justify-content: space-between;
	}
	@include mq($from: mobile, $until: desktop){
		flex-wrap: wrap;
			flex-direction: row;
			justify-content: space-between;
	}
	li{
		padding: 0;
		margin: 0;
		@include mq($from: mobile, $until: tablet){
			width: 50%;
			margin-bottom: 20px;
			&:last-child{
				margin-bottom: 0;
			}
		}
		@include mq($from: tablet, $until: desktop){
			width: 33.3%;
			margin-bottom: 25px;
		}
		a{
			display: block;
			opacity: .7;
			transition: .2s ease-in-out;
			&:hover, &:focus{
				opacity: 1;
			}
		}
		img{
			display: block;
			@include mq($from: mobile, $until: desktop){
				margin: auto;
			}
		}
	}
}