.application{
	padding: 65px 0 55px;
	background: #fff2d2;
	position: relative;
	&:before{
		content: "";
		position: absolute;
		top: -6px;
		left: 0px;
		width: 100%;
		height: 6px;
		background: url(../img/line-split.png) repeat-x;
	}
	&__title{
		margin: 0 0 35px;
		color: #292929;
		font-size: 32px;
		line-height: 42px;
		text-align: center;
		@include mq($from: mobile, $until: tablet){
			margin-bottom: 20px;
			font-size: 26px;
			line-height: 30px;
		}
	}
	&__text{
		margin: 0 0 50px 0;
		color: #606060;
		font-weight: 300;
		text-align: center;
		font-size: 18px;
		line-height: 22px;
		@include mq($from: mobile, $until: tablet){
			margin-bottom: 30px;
			font-size: 14px;
		}
	}	
}
