.application-form{
	width: 550px;
	margin: auto;
	@include mq($from: mobile, $until: mobileHorizontal){
		width: 100%;
	}
	&__line{
		display: flex;
		justify-content: space-between;
		margin-bottom: 25px;
		@include mq($from: mobile, $until: tablet){
			flex-direction: column;
			align-items: center;
			margin-bottom: 0;
		}
	}
	input{
	  	width: 265px;
		height: 50px;
		padding: 10px 20px;
		color: #9a9a9a;
		border-radius: 30px;
		border: 1px solid #dadada;
		outline: none;
		@include mq($from: mobile, $until: mobileVertical){
			margin-bottom: 20px;
			width: 100%;
		}
		@include mq($from: mobileVertical, $until: mobileHorizontal){

		}
		@include mq($from: mobileVertical, $until: desktop){
			display: block;
			margin: 0 auto 20px;
		}
		&:focus{
			border: 1px solid #ffbd1f;
		}
	}
	&__btn {
	    display: block;
	    padding: 13px 10px 12px 48px;
	    width: 265px;
    	height: 50px;
	    background: #ffbd1f;
	    border: 1px solid #ffbd1f;
	    border-radius: 30px;
	    outline: none;
	    cursor: pointer;
	    text-decoration: none;
	    box-shadow: 0px 10px 30px 0px rgba(0, 126, 250, 0.3);
	    transition: .2s ease;
	    @include mq($from: mobile, $until: mobileVertical){
			width: 100%;
		}
	    span{
	    	position: relative;
	    	color: #292929;
	    	font-size: 16px;
	    	transition: .3s ease-in;
	    	&:after {
			    content: "";
			    position: absolute;
			    top: 4px;
			    right: -36px;
			    width: 24px;
			    height: 18px;
			    background: url(data:image/png;base64,) no-repeat;
				transition: left .2s ease-in-out;
			}
	    }
		&:hover, &:focus{
			span{
				&:after {
					right: -40px;
				}
			}
		}
	}
}